import { useStore } from 'vuex';
import { computed } from 'vue';

import { Sidebar } from '@/features/filter/sidebar';
import { SelectFilter } from '@/features/filter/select';

const route = useRoute();
const store = useStore();

const productSlug = computed(() => String(route.params.slug));
const productData = computed(() => store.getters['products/getProductBySlug'](productSlug.value));

</script>
